<template>
  <div class="electricityBehavior">
    <div class="top-content">
      <div
        class="content-menu"
        @click="changeActive(index+1,item)"
        v-for="(item,index) in menuData"
        :class="active==='0'+(index+1)?'active':''"
        :key="index"
      >
        <!-- <el-tooltip
          class="item"
          effect="dark"
          :disabled="item.name.length<7"
          :content="item.name"
          placement="top"
        >-->
        <span class="text">{{item.name}}</span>
        <!-- </el-tooltip> -->
        <div v-show="active!=='0'+(index+1)" class="shadow"></div>
      </div>
    </div>
    <div class="bottom-content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>

export default {
  components: {

  },
  name: "electricityBehavior",
  //   mixins: [Interface],
  data() {
    return {
      active: "01",
      menuData: [
        // { name: '居民用电行为全景', value: '01', path: '/electricityBehavior/panoramicAnalysis' },
        { name: '居民用电行为分析', value: '02', path: '/electricityBehavior/electricActionAnalysis' },
        { name: '居民用电差异化服务策略库', value: '03', path: '/electricityBehavior/differentiationService' },
        { name: '居民用电优化建议', value: '04', path: '/electricityBehavior/optimizationMind' },
        // { name: '推荐匹配管理', value: '02', path: '/recommendManagement/menuType/7' },
      ]
    }
  },
  mounted() {
    this.$router.push({ path: this.menuData[0].path })

  },
  methods: {
    changeActive(index, data) {
      this.active = '0' + index
      this.$router.push({ path: data.path })
    },
  },
}
</script>

<style scoped lang="scss">
.electricityBehavior {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  .top-content {
    width: 100%;
    font-size: 16px;
    display: flex;
    background: url("../../assets/img/标题背景.png") no-repeat center;
    background-size: 100% 100%;
    position: fixed;
    z-index: 9; // justify-content: center;
    // border: 1px solid #d9d9d9;
    .active {
      .text {
        color: #4b71a4;
        font-weight: 700;
      }
    }
    .content-menu {
      float: left;
      padding: 0 20px;
      color: gray;
      line-height: 30px;
      cursor: pointer;
      border-bottom: 30px solid #fff;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      width: 185px;

      position: relative;
      .text {
        font-size: 14px;
        position: absolute;
        width: 185px;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .shadow {
        position: absolute;
        background: linear-gradient(0deg, #e1e7f0, hsla(0, 0%, 100%, 0));
        top: 22px;
        left: -9px;
        z-index: 9;
        height: 8px;
        width: 243px;
      }
    }
  }
  .bottom-content {
    padding: 40px 10px 0 10px;
    box-sizing: border-box;
    width: 100%;
    height: calc(100% - 50px);
    background-color: #fff;
  }
}
</style>